import React, { Component } from 'react';
import { CommitCard } from '../CommitCard';
var _ = require('underscore');

import {Doughnut, Line} from 'react-chartjs-2';

const data = {
	labels: [
		'Red',
		'Green',
		'Yellow'
	],
	datasets: [{
		data: [300, 50, 100],
		backgroundColor: [
		'#FF6384',
		'#36A2EB',
		'#FFCE56'
		],
		hoverBackgroundColor: [
		'#FF6384',
		'#36A2EB',
		'#FFCE56'
		]
	}]
};

export default class Person extends Component{
  constructor(props){
    super(props)
    this.id = React.createRef()
    this.name = this.props.name
    this.commits = this.props.commits
    this.numberOfCommits = this.props.commits.length
    this.busiestDay = "";
    this.busiestDayCommits = 0;
    this.state = {
      commitByDate: {},
      compiledData: {}
    }
  }

  // shouldComponentUpdate(nextProps, nextState){
  //   if (this.state.compiledData != nextState.compiledData){
  //     console.log("updated")
  //     return true;
  //   }
  //   return false
  // }

  componentDidMount(){
    this._getCommits()
  }

  _getCommits = () => {
    let date = /(\d{1,2})\s(\w{3,})\s(\d{1,4})\s/gm
    const commits = this.commits

    var promise = new Promise(function(resolve,reject){
      var currDates = {}
      _.map(commits, function(commit){
        let commitDate = commit.author.date.match(date)[0]
        if (currDates[commitDate]){
          let prevCommits = currDates[commitDate].push(commit)

        } else{
          currDates[commitDate] = [commit]
        }
      })

      resolve(currDates)
    })

    promise.then((currDates) => {
      this.setState({
        commitByDate: currDates
      })
      return(currDates)
    })

    promise.then((dates)=>{
      let labels = Object.keys(dates)
      let busiestDayDate = ""
      let busiestDayCommits = 0
      let countData = _.map(labels, function(date){
        let len = dates[date].length
        if (len > busiestDayCommits){
          busiestDayDate = date
          busiestDayCommits = len
        }
        return len
      });

      let dataSet = {
        'labels': labels,
        'datasets':  [{
          'label': `Number of commits`,
          'borderCapStyle': 'butt',
          'fill': true,
          'borderColor': 'rgba(75,192,192,1)',
          'backgroundColor': 'rgba(75,192,192,.4)',
          'data': countData
        }]
      };
      this.setState({
        busiestDay: busiestDayDate,
        busiestDayCommits: busiestDayCommits
      })
      return dataSet

    }).then((compData) => {
      this.setState({
        compiledData: compData
      })

      return;
    })

  }

  render(){
    return(
      <div>
        <h1>Hello {this.name}</h1>

        <h2>You have made {this.numberOfCommits} commits</h2>

        {this.busiestDayCommits !== 0 &&
          <h2>You made {this.busiestDayCommits} commits on {this.busiestDay}</h2>}

        <h3>Number of commits per day</h3>
        <Line data={this.state.compiledData} />

        <h3>All commits</h3>
        {this.commits.map(com =>
          <div>
            <CommitCard commit={com} />
            <br/>
          </div>
        )}
      </div>
    )
  }
}
