import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = {
  card: {
    maxWidth: 275*2
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
};


class CommitCard extends Component{

  constructor(props){
    super(props)
    this.commit = this.props.commit
  }

  render(){
    const bull = <span className={styles.bullet}>•</span>;
    const commitLink = "https://github.com/chishankar/WebCache/commit/"+this.commit.commit
    return (
      <Card className={styles.card}>
        <CardContent>
          <Typography className={styles.title} color="textSecondary" gutterBottom>
            {this.commit.commit}
          </Typography>
          <Typography variant="h5" component="h2">
            {this.commit.subject}
          </Typography>
          <Typography className={styles.pos} color="textSecondary">
            {this.commit.refs != "" && <div>Branch: {this.commit.refs}</div>}
          </Typography>
          <Typography component="p">
            {this.commit.author.name}
            <br />
            {this.commit.author.date}
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small" href={commitLink} target="_blank">See Commit</Button>
        </CardActions>
      </Card>
    );
  }
}

export default withStyles(styles)(CommitCard);
